@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: aliceblue;
  border: 1px solid rgba(0, 0, 0, 0.233);
  width: 18.75rem;
  border-radius: 12px;
  padding-bottom: 20px;
}

.Bgimage {
  width: 100%;
  height: 200px;
}
.image {
  object-position: 50% 50%;
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 12px 12px 0 0;
}
.caption {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: flex-start;
  padding: 10px;
  font-family: "Poppins", sans-serif;
}
.description {
  padding: 10px 0;
  flex-direction: column;
  text-align: start;
}
.statusMK {
  padding-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
}
.status {
  color: green;
}
.name {
  margin-top: 10px;
}
.buttonDetail {
  padding: 9px 12px;
  background-color: rgb(238, 234, 0);
  border-radius: 5px;
  border: 1px solid rgb(202, 202, 202);
  cursor: pointer;
  color: rgb(56, 56, 56);
  font-size: 0.8em;
  font-weight: bold;
}
.buttonDetail:hover {
  background-color: rgb(0, 194, 168);
  color: rgb(255, 255, 255);
}
