@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');
.container {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  margin-top: 100px;
  padding: 20px 0;
}
.judul {
  font-size: 2em;
  font-weight: bold;
  font-family: "Poppins", sans-serif;
  color: rgb(0, 136, 106);
}
.description{
    font-size: 1.2em;
    font-family: "Poppins", sans-serif;
    color: rgb(77, 77, 77);
    font-weight: 600;
    margin-bottom: 70px;
}
.product {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
.form{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
