@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.card {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: aliceblue;
  border: 0.5px solid rgba(0, 0, 0, 0.055);
  width: 18.75rem;
  border-radius: 12px;
  padding: 20px;
  gap: 20px;
  flex-basis: 33.333%;
  flex-grow: 0;
  flex-shrink: 0;
}
.profile {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
}
.name {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: bold;
}
.image {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  object-fit: cover;
}
.description {
  width: 100%;
  padding: 10px 0;
}
.rating{
    margin-bottom: 5px;
}
