@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.container {
  width: 80%;
  margin: 0 auto;
  border: 1px solid rgba(226, 226, 226, 0.534);
  border-radius: 9px;
  background-color: rgb(247, 251, 255);
  padding-bottom: 40px;
}
.product {
  width: 100%;
}
.description{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    margin-top: 20px;
}
.nameResto{
    font-family: "Poppins", sans-serif;
    font-size: 24px;
    font-weight: bold;
}
.rating{
    font-size: 24px;
}
.image {
  width: 100%;
  height: 600px;
  object-position: 50% 50%;
  object-fit: cover;
}
.reviewUser {
  display: flex;
  flex-direction: row;
  gap: 20px;
  white-space: wrap;
  overflow-x: auto;
  padding: 20px;
}
