@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.container {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background-color: rgb(43, 43, 43);
  padding: 250px 0;
}
.banner{
    margin: 0 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.judul {
  font-size: 4em;
  font-weight: bold;
  text-decoration: none;
  color: rgb(224, 221, 0);
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
}
.content_text {
  font-size: 24px;
  margin-bottom: 40px;
  font-family: "Poppins", sans-serif;
  color: white;
}
.content_button{
    padding: 15px 25px;
    color: rgb(56, 56, 56);
    font-size: 20px;
    border-radius: 9px;
    border: 1px solid rgb(56, 56, 56);
    cursor: pointer;
    background-color: rgb(238, 234, 0);
}
.content_button:hover{
    background-color: rgb(0, 194, 168);
    color: rgb(255, 255, 255);
}